"use client";
import React from "react";
import { useEffect, useState, useRef } from "react";
import Image from "next/image";

const page = () => {
  const route = useRouter();
  return (
    <div className="flex items-start justify-center gap-10 min-h-screen p-10 ">
      page{" "}
      <div className="header">
        <div>
          <Image
            src={"/assets/suggestions/icon-suggestions.svg"}
            width={24}
            height={24}
            alt="suggestions"
          />
          <h3>Roadmap </h3>
        </div>
        <button onClick={() => route.push("/add")}>+ Add Feedback</button>
      </div>
    </div>
  );
};

export default page;
